.tab {
  display: flex;
  flex-direction: column;
  margin: 0 auto 1rem;
  font-size: 1.4em;
}

.tab__line {
  display: flex;
  flex: 0 0 100%;
  position: relative;
  align-items: center;
}

.tab__line__line {
  position: absolute;
  z-index: 0;
  top: 50%;
  left: 0;
  height: 1px;
  width: 100%;
  background-color: grey;
}

.tab__line__steps {
  display: flex;
  width: 100%;
  height: 1.4em;
}

.tab__step {
  position: relative;
  z-index: 1;
  display: flex;
  flex: 1 1;
  align-items: center;
}

.tab__step:first-child {
  border-left: 1px solid grey;
}

.tab__step:nth-child(8n) {
  border-right: 1px solid grey;
}

.tab__step input {
  width: 1em;
  height: 1em;
  font-size: 1em;
  text-align: center;
  margin: 0 auto;
  border: none;
  background-color: #f6f6f6;
}

.tab__step--is-current input {
  background-color: turquoise;
}

.playhead {
  height: 1em;
  background-color: grey;
  width: 2px;
  position: relative;
}

